import { FC } from 'react';
import Accordion from '@mui/material/Accordion';
import AccordionSummary from '@mui/material/AccordionSummary';
import AccordionDetails from '@mui/material/AccordionDetails';
import Typography from '@mui/material/Typography';
import ExpandMoreIcon from '@mui/icons-material/ExpandMore';
import { Wrapper } from './styles-accordion';
import InAccordionTargetMap from './in-accordion/in-accordion';

const AccordionTargetMap: FC<{}> = () => {
  return (
    <Wrapper>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Typography>2022 maqsadlarim</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <InAccordionTargetMap />
        </AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Typography>2021 maqsadlarim</Typography>
        </AccordionSummary>
        <AccordionDetails>
          <InAccordionTargetMap />
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  );
};

export default AccordionTargetMap;
