import { FC } from 'react';
import { useForm } from 'react-hook-form';
import StartToFinish from '../../../common/start-to-finish/start-to-finish';
import { CreateTargetForm, CreateTargetProps } from './types-create-target';
import {
  Input,
  LightButton,
  LightSelect,
  UploadFile,
} from '@components/common';
import { ModalButton, Title } from '../../staff/optional-styles';
import { HorizontalSeparator, VerticalSeparator } from '@components/common/separators/sepatators';
import {
  Wrapper,
  Img,
  ButtonGroup,
  Form,
  Card,
  CardItem,
} from './styles-create-target';

import { Avatar } from '@mui/material';
import addUser from '@assets/images/dashboard/target-add-user.svg';
import target_award from '@assets/images/dashboard/target-award.svg';
import statistics from '@assets/images/dashboard/statistics.svg';
import x_icon from '@assets/images/dashboard/target-x.svg';

const mock = [1,1,1,1,1,1,1,1,1]

const TaskOpening: FC<CreateTargetProps> = () => {
  const { handleSubmit, control } = useForm<CreateTargetForm>();
  const onChange = (type: string, value: any) => {
    console.log('type: ', type);
  };
  return (
    <Wrapper>
      <Title>Maqsad yaratish</Title>
      <ButtonGroup>
        <LightButton icon={<Img src={addUser} alt="icon"/>} text="Ijrochi" />
        <HorizontalSeparator width="30px" />
        <LightButton icon={<Img src={target_award} alt="icon"/>} text="Ijrochi" />
        <HorizontalSeparator width="30px" />
        <LightButton icon={<Img src={statistics} alt="icon"/>} text="Ijrochi" />
      </ButtonGroup>
      <Form>
        <Input
          name="targetName"
          placeholder="Nomini kiriting"
          title="Maqsad nomi"
          control={control}
          onChange={onChange}
        />
        <Input
          name="description"
          placeholder="Izoh kiriting"
          title="Izoh"
          control={control}
          onChange={onChange}
        />
      </Form>
      <StartToFinish hasPin />
      <ButtonGroup>
        <UploadFile />
        <HorizontalSeparator width="30px" />
        <LightButton icon={<Img src={statistics} />} text="Kuzatuvchilar" />
        <HorizontalSeparator width="30px" />
        <LightSelect />
      </ButtonGroup>
      <Card>
        {mock.map((index) => (
          <CardItem key={index}>
            <Avatar />
            <p>Erkaboy Toirov</p>
            <img src={x_icon} alt='icon'/>
          </CardItem>
        ))}
      </Card>
      <VerticalSeparator height='50px'/>
      <ModalButton width="35%" color="#fff" background="#0496FF" padding='18px 0'>
        Saqlash
      </ModalButton>
    </Wrapper>
  );
};

export default TaskOpening;
