import { FC, useState } from 'react';
import { TargetMapProps } from './types-target-map';
import { Button, TopCard, Wrapper } from './styles-target-map';
import AccordionTargetMap from './accordion/accordion';
import { GeneralContainer, UniversalModal } from '@components/common';
import CreateTarget from './create-target/create-target';

const TargetMap: FC<TargetMapProps> = () => {
  const [open, setOpen] = useState<boolean>(false);
  const handleClose = () => {
    setOpen(false);
  };
  return (
    <GeneralContainer>
      <Wrapper>
        <TopCard>
          <h1>Maqsadlar xaritasi</h1>
          <Button onClick={() => setOpen(true)}>+ Maqsad qo'yish</Button>
        </TopCard>
        <AccordionTargetMap />
        <UniversalModal open={open} handleClose={handleClose}>
          <CreateTarget />
        </UniversalModal>
      </Wrapper>
    </GeneralContainer>
  );
};

export default TargetMap;
