import { FC } from "react";
import Accordion from "@mui/material/Accordion";
import AccordionSummary from "@mui/material/AccordionSummary";
import AccordionDetails from "@mui/material/AccordionDetails";
import Typography from "@mui/material/Typography";
import ExpandMoreIcon from "@mui/icons-material/ExpandMore";
import { HorizontalSeparator } from "@components/common/separators/sepatators";
import {
  Card,
  IconBox,
  IconCard,
  LabelBox,
  Line,
  RightCard,
  StatusCard,
  Wrapper,
} from "./styles-in-accordion";

import blue_work from "@assets/images/dashboard/target-blue-work.svg";
import colored_accept from "@assets/images/dashboard/target-colored-accept.svg";
import accept from "@assets/images/dashboard/target-accept.svg";
import gps from "@assets/images/dashboard/target-gps.svg";
import load from "@assets/images/dashboard/target-load.svg";
import work from "@assets/images/dashboard/target-work.svg";
import edit from "@assets/images/dashboard/target-edit.svg";

const InAccordionTargetMap: FC<{}> = () => {
  return (
    <Wrapper>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel1a-content"
          id="panel1a-header"
        >
          <Card>
            <IconCard forGps>
              <IconBox>
                <img src={gps} alt="icon" />
              </IconBox>
              <Line></Line>
            </IconCard>
            <RightCard forGps>
              <LabelBox>
                <h1>2022 maqsadlarim</h1>
                <StatusCard>
                  <div>
                    <img src={load} alt="icon" />
                    <span>0%</span>
                  </div>
                  <HorizontalSeparator width="30px" />
                  <div>
                    <img src={work} alt="icon" />
                    <span>2ta</span>
                  </div>
                </StatusCard>
              </LabelBox>
              <img src={edit} alt="icon" />
            </RightCard>
          </Card>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse
            malesuada lacus ex, sit amet blandit leo lobortis eget.
          </Typography>
        </AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel2a-content"
          id="panel2a-header"
        >
          <Card>
            <IconCard>
              <IconBox>
                <img src={blue_work} alt="icon" />
              </IconBox>
              <Line></Line>
            </IconCard>
            <RightCard>
              <LabelBox>
                <h1>2022 maqsadlarim</h1>
                <StatusCard>
                  <div>
                    <img src={load} alt="icon" />
                    <span>0%</span>
                  </div>
                  <HorizontalSeparator width="30px" />
                  <div>
                    <img src={accept} alt="icon" />
                    <span>1ta</span>
                  </div>
                </StatusCard>
              </LabelBox>
              <img src={edit} alt="icon" />
            </RightCard>
          </Card>
        </AccordionSummary>
        <AccordionDetails></AccordionDetails>
      </Accordion>
      <Accordion>
        <AccordionSummary
          expandIcon={<ExpandMoreIcon />}
          aria-controls="panel3a-content"
          id="panel3a-header"
        >
          <Card>
            <IconCard>
              <IconBox>
                <img src={colored_accept} alt="icon" />
              </IconBox>
              <Line></Line>
            </IconCard>
            <RightCard>
              <LabelBox>
                <h1>2022 maqsadlarim</h1>
                <StatusCard>
                  <div>
                    <img src={load} alt="icon" />
                    <span>0%</span>
                  </div>
                  <HorizontalSeparator width="30px" />
                  <div>
                    <img src={accept} alt="icon" />
                    <span>0ta</span>
                  </div>
                </StatusCard>
              </LabelBox>
              <img src={edit} alt="icon" />
            </RightCard>
          </Card>
        </AccordionSummary>
        <AccordionDetails>
          <Typography>
            Lorem ipsum dolor sit amet, consectetur adipiscing elit. Suspendisse
            malesuada lacus ex, sit amet blandit leo lobortis eget.
          </Typography>
        </AccordionDetails>
      </Accordion>
    </Wrapper>
  );
};

export default InAccordionTargetMap;
