import styled from 'styled-components';

export const Wrapper = styled.div`
  background: #ffffff;
  border-radius: 7px;
  padding: 30px;
  width: 1200px;
  max-height: 100vh;
  overflow: auto;
`;

export const ButtonGroup = styled.div`
  display: flex;
  justify-content: flex-start;
  align-items: center;
  margin-top: 30px;
`;

export const Img = styled.img`
  margin-right: 12px;
`;

export const Form = styled.form`
  width: 100%;
  display: grid;
  grid-template-columns: 0.4fr 0.4fr;
  column-gap: 70px;
`;

export const Card = styled.div`
  width: 100%;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  column-gap: 20px;
  row-gap: 25px;
  margin-top: 40px;
`;

export const CardItem = styled.div`
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 80px;
  background: #e6e9ef;
  border-radius: 7px;
  p {
    font-weight: 500;
    font-size: 20px;
    line-height: 24px;
    color: #000000;
  }
  img{
      cursor: pointer;
  }
`;
