import styled from "styled-components";

export const Wrapper = styled.div`
  width: 100%;
  min-height: 85vh;
  padding: 35px 30px;
  background: #ffffff;
  border-radius: 7px;
`;

export const TopCard = styled.div`
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
  h1 {
    font-weight: 600;
    font-size: 24px;
    line-height: 29px;
    color: #000000;
  }
`;

export const Button = styled.button`
  border: none;
  padding: 12px 15px;
  background: #0496ff;
  border: 1px solid #0496ff;
  border-radius: 60px;
  min-width: 214px;
  font-weight: 500;
  font-size: 20px;
  line-height: 24px;
  display: flex;
  align-items: center;
  color: #ffffff;
  cursor: pointer;
`;


